.event_card {
    width: 17rem;
    height: max-content;
    height: 17rem;
    border-radius: 1.5rem;
    padding-bottom: 1.5rem;
    background: #ffffff;
    border: 1px solid #e5e5e5;
    overflow: hidden;
  }
  
  p{
      padding-inline: .8rem;
  }
  
  .card>p:nth-of-type(1){
      font-size: 16px;
      font-weight: 500;
  }
  
  .card>p:nth-of-type(2){
      font-size: 14px;
      color: #F16621;
  }
  .card>p:nth-of-type(3){
      font-size: 12px;
  }
  
  img {
    width: 100%;
  }
  
  .register_button {
    box-sizing: border-box;
    border: 1px solid #00c853;
    border-radius: 10px;
    margin: 1rem;
    padding: .4rem;
    color: #00c853;
    justify-self: center;
    text-align: center;
    cursor: pointer;
    font-size: 16px;
  }
  